.movie {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 20px;
  padding-bottom: 40px;

  @media screen and (max-width: 767px) {
    justify-content: center;
  }

  @media screen and (min-width: 768px) {
    gap: 32px;
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @media screen and (min-width: 1280px) {
    gap: 32px 16px;
  }
}
.movie__item {
  max-width: 280px;

  @media screen and (min-width: 768px) {
    max-width: 336px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 395px;
  }
}

.movie__image {
  min-width: 100%;
  height: 398px;
  object-fit: cover;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    height: 455px;
  }

  @media screen and (min-width: 1280px) {
    height: 574px;
  }
}

.movie__title {
  margin-top: 10px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  text-transform: uppercase;
}
.movie__details {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  text-transform: capitalize;
  color: #ff6b08;
}
.movie__genre {
  margin-right: 10px;
}
.movie__year-of-relise {
  margin-left: 10px;
}
.movie__rating {
  margin-left: 10px;
  padding: 0 7px 3px;
  border-radius: 10px;
  color: #ffffff;
  background-color: #ff6b08;
}
